import React, {FC, useEffect} from 'react';
import { connect } from 'react-redux';
import {Avatar, Button, Card, Icon, Tag, Typography, Skeleton} from 'antd';

import {selectScreen} from '../../helpers/screen.helper';
import {IReduxState} from '../../reducers';
import {IStudentExtraDetails} from '../../types/api.student.type';
import {loadStudentExtraDetails} from '../../actions/student.action';
import {IUserMeta} from '../../types/api.type';
// @ts-ignore
import {ReactComponent as StackOverflowIcon} from '../../assets/svgs/stackoverflow-icon.svg';
import SideDrawerForm from '../sideDrawerForm';


interface IStateProps {
  extraDetails: IStudentExtraDetails | undefined | null;
  user: IUserMeta | undefined;
}

interface IDispatchProps {
  loadExtraDetails: any
}

interface IProps extends IStateProps, IDispatchProps {}

const {Title, Text, Paragraph} = Typography;

const getYear = (year: number): string => {
  switch (year) {
    case 1:
      return '1st';
    case 2:
      return  '2nd';
    case 3:
      return '3rd';
    default:
      return `${year}th`
  }
};

const GetIcon: FC<any> = ({type}: any): any => {
  switch (type) {
    case 'G':
      return <Icon type='github' theme='filled' style={{marginLeft: 15}} />;
    case 'W':
      return <Icon type='link' style={{marginLeft: 15}} />;
    case 'S':
      return  (
        <StackOverflowIcon style={{
          fill: '#595959',
          height: 30,
          width: 30,
          marginLeft: 15
        }} />
      );
    case 'B':
      return <Icon type='behance' theme='filled' style={{marginLeft: 15}} />;
    case 'M':
      return <Icon type='mail' theme='filled' style={{marginLeft: 15}} />;
    case 'L':
      return <Icon type='linkedin' theme='filled' style={{marginLeft: 15}} />;
    default:
      return <Icon type='link' theme='filled' style={{marginLeft: 15}} />;
  }
};

const ProfileCard: FC<IProps> = ({extraDetails, loadExtraDetails, user}: IProps) => {

  useEffect(() => {
    loadExtraDetails();
  }, [loadExtraDetails]);

  if(extraDetails === undefined || user === undefined)
    return (
      <Card>
        <Skeleton />
      </Card>
    );

  if(extraDetails === null)
    return (
      <Card>
        <Title level={4}>You have not added your extra info yet</Title>
        <Button type='primary' size='large'>
          Click to add
        </Button>
      </Card>
    );

  return (
    <Card>
      <div style={{display: 'flex'}}>
        <div style={{
          display: 'flex',
          flexWrap: 'wrap',
          textAlign: selectScreen('center', null),
          justifyContent: selectScreen('space-around', null)
        }}>
          <Avatar icon='user' size={64} />
          <div style={{
            marginLeft: selectScreen(0, 5, 10)
          }}>
            <Title style={{marginBottom: 0}}>
              {user.name}
            </Title>
            <Text strong>
              {extraDetails.tag_line}
            </Text>
          </div>
        </div>
      </div>
      <br />
      <Text>
        <b>
          {extraDetails.student.course}
          &nbsp;&nbsp;&middot;&nbsp;&nbsp;
          {`${getYear(extraDetails.student.year)} year`}
        </b>
      </Text>
      <Paragraph type='secondary' editable>
        {extraDetails.about}
      </Paragraph>
      <div
        style={{
          display: 'flex',
          alignItems: 'center',
          position: 'relative',
          left: -15
        }}
      >
        {extraDetails.profiles.map(profile => (
          <a
            href={profile.link}
            style={{fontSize: 30, color: '#666',}}
            rel='noopener noreferrer'
            target='_blank'
          >
            <GetIcon type={profile.type} />
          </a>
        ))}
      </div>
      <div>
        Skills:&nbsp;&nbsp;
        {extraDetails.skills.map(skill => (
          <Tag>
            {skill}
          </Tag>
        ))}
      </div>
      <SideDrawerForm
        // title='Extra Details'
        render={() => (
          <h1>
            hjfhgefdhgghv
          </h1>
        )}
      >
        <Button style={{float: 'right'}} type='link'>
          <Icon type='edit' />
          Edit
        </Button>
      </SideDrawerForm>
    </Card>
  );
};


const mapStateToProps = (state: IReduxState): IStateProps => ({
  extraDetails: state.student.extraDetails,
  user: state.auth.user
});

const mapDispatchToProps = (dispatch: any): IDispatchProps => ({
  loadExtraDetails: () => dispatch(loadStudentExtraDetails())
});

export default connect(mapStateToProps, mapDispatchToProps)(ProfileCard);
